body{
  background-image: url("https://image.cleveland.com/home/cleve-media/width600/img/ent_impact_home/photo/beyoncejay-z-7c71fa60ba5dbd8b.jpg");
  background-repeat:no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-color:black;
}

button{
  height:35px;
  width:95px;
  position: fixed; 
  top: 24%;
  right: 45%;
  font-size:12px;
  background-color: rgb(59,53,129);
  color:white;
  font-family:'courier new';
}

img{
  height: 170px;
  text-align: center;
  position: fixed;;
  top: 30%;
  right:30%;
}

#info{
  font-size:15px;
  font-family:'courier new';
  color:white;
  text-shadow:
		-1px -1px 0  rgb(59,53,129),
		1px -1px 0  rgb(59,53,129),
		-1px 1px 0  rgb(59,53,129),
		1px 1px 0  rgb(59,53,129);
  text-align: center;
  position: fixed;;
  top: 58%;
  right:27%;
}




